import * as React from 'react'
import { connect } from 'react-redux'
import { ApplicationState } from '../../../store'
import * as types from '../../../store/types'
import * as userProfile from '../../../store/userProfile'
import Select from '../../formElements/select'

type props = {
    setUserProfile: (object: types.userProfile) => void
}

const departments = [
    { value: "Department of Public Works", label: "Department of Public Works" },
    { value: "Bureau of Fire", label: "Bureau of Fire" }
]
export class SelectDepartment extends React.Component<props, any> {
    constructor(props) {
        super(props)
        this.state = {
            department: ''
        }
    }
    
    render() {
        return (
            <div>
                <Select
                    value={this.state.department}
                    header=""
                    placeholder='Select department or bureau'
                    onChange={department => this.props.setUserProfile({ department: department.value })}
                    multi={false}
                    options={departments}
                />
            </div>
        )
    }
}

export default connect(
    (state: ApplicationState) => ({
        ...state.userProfile
    }),
    ({
        ...userProfile.actionCreators
    })
)(SelectDepartment as any)


